'use strict';

const string
  = '«Тренуйся з тими, хто сильніший. Не здавайся там, де здаються інші. І переможеш там, де перемогти не можливо», — Брюс Лі';
const str = string.split('');
const el = document.getElementById('str');

(function animate() {
  str.length > 0 ? (el.innerHTML += str.shift()) : clearTimeout(running);

  var running = setTimeout(animate, 40);
})();
